@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: blueviolet;
    color: white;
    padding:10px;
    font-size: 1.5rem;
}

#logo{
    margin: 0px 20px;
    padding:10px 15px;
    background-color: white;
    color: blueviolet;
    border-radius: 24px 4px;
    font-size: 1.2rem;
    font-size: 1.5rem;
}
a{
    color:white;
    text-decoration: none;
}
#nav-content{
    margin:0px 20px;
}
#nav-content ul{
    list-style: none;
    display: flex;
    flex-direction: row;
}
#nav-content ul li{
    margin: 0px 10px;
    font-size: 1rem;
    font-size: 1.3rem;
}
.inp{
    margin:30px 20px;
    border: none;
    outline: none;
    border-bottom: 2px solid blueviolet;
    color: black;
    font-size: 1.2rem;
}
.inp::-webkit-input-placeholder{
    color: rgba(126, 126, 126, 0.466);
}
.inp:-ms-input-placeholder{
    color: rgba(126, 126, 126, 0.466);
}
.inp::placeholder{
    color: rgba(126, 126, 126, 0.466);
}
form{
    display: flex;
    flex-direction: column;
    min-width: 70%;
    padding: 10px;
    /* justify-content: center; */
    align-items: center;
    font-size: 1.2rem;
    margin:50px;
}
form h1{
    color: blueviolet;
}
.btn{
    color: white;
    background-color: rgb(0, 132, 255);
    padding: 10px 20px;
    font-size: 1rem;
    outline: none;
    border: none;
}
@media only screen and (max-width: 600px) {
    #nav-content ul li{
        font-size: 0.9rem;
    }
    #logo{
        font-size: 1.1rem;
    }
}
* {
    margin: 0;
    padding: 0;
    font-family: arial;
    box-sizing: border-box;
  }
  
  
  table {
      margin: auto;
      margin-top: 100px;
    min-width: 70%;
    box-shadow: -1px 12px 12px -6px rgba(0, 0, 0, 0.5);
    font-family: 'Poppins', sans-serif;
  }
  
  table,
  td,
  th {
    padding: 6px;
    /* border: 1px solid lightgray; */
    border-collapse: collapse;
    text-align: center;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
  }
  .topic{
    font-family: 'Poppins', sans-serif;
    font-size: 2.5rem;
    text-align: center;
    margin-top: 50px;
    text-decoration:underline;
  }
  
  td {
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
  }
  
  th {
    background-color: blue;
    color: white;
    font-family: 'Poppins', sans-serif;
  }
  
  tr:nth-child(odd) {
    background-color: blueviolet;
    color: white;
    font-family: 'Poppins', sans-serif;
  }
  
  tr:nth-child(even) {
    background-color: #ededed;
    color: blueviolet;
    font-family: 'Poppins', sans-serif;
  }
  
  .btn {
    padding: 5px 19px;
    color: black;
    background-color: white;
    margin: 13px;
    font-family: 'Poppins', sans-serif;
  }
  
  .btn:hover {
    cursor: pointer;
  }
  
  .btn1 {
    padding: 5px 19px;
    color: black;
    background-color: white;
    font-family: 'Poppins', sans-serif;
  }
  
  .btn1:hover {
    cursor: pointer;
  }
  
